.style-guide-page {
    .color {
        display: inline-block;
        margin: 10px;
        text-align: center;
        max-width: 80px;

        .color-name {
            font: {
                size: 16px;
                weight: bold; }
            text-transform: capitalize; }

        .hexcode {
            font: {
